
body {
  color: #ffffff;
}
h1,h2,h3,h4,h5,h6 {
  font-weight: 200;
}
.weather-header {
  text-align: center;
  padding: 30px;
}
.weather-header h1 {
  font-size: 3em;
}
.weather-header h2 {
  font-size: 1.8em;
}
.weather-header h3 {
  font-size: 1.3em;
}
.content-header, .content-footer {
  padding-left: 15px;
  padding-right: 15px;
}
.sub-title {
  color: #d0cfd0;
}
.content-footer {
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  max-height: 80px;
  height: 80px;
  margin-top: 5px;
  text-align: center;
  padding-top: 8px;
}
.weather-footer {
  margin-top: 5px;
}
.popup0 {
  padding-bottom:15px;
  height:200px;
}
.addrSelectorPopup {
  width:100%;
  height:100%;
}
.popup2 {
  padding-bottom:15px;
  height:400px;
}
.position-vertical-demo {
  background-color: #ffe26d;
  color: #000;
  text-align: center;
  padding: 15px;
}
.position-horizontal-demo {
  position: relative;
  height: 100%;
}
.vux-close {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%) scale(4);
          transform: translateX(-50%) translateY(-50%) scale(4);
  color: #000;
}
.grid {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.grid-cell {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.bg {
  border: 0px;
  position: fixed;
  z-index: -1;
  width: 100%;
  height: calc(100vh + 180px);
}
.weather {
  font-size: .8em;
}
.weui-label {
  color: #000;
}
.vux-popup-picker-value {
  color: #000!important;
}
.repos {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background-image: url('../assets/location.png');
}
.repos:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}




